or:#3b3b3b;text-decoration:none}header nav .nav-links .nav-link a:hover{color:#6730ec}@media (min-width: 721px){header nav .nav-links{padding:0;position:relative;height:auto;height:initial;flex-direction:row;transform:translateX(0%);justify-content:space-between;background:transparent;width:475px;max-width:100%}header nav .nav-links .nav-link{margin-left:auto}}@media (min-width: 960px){header nav .nav-links{width:600px}}header nav .burger{cursor:pointer;z-index:9000}@media (min-width: 721px){header nav .burger{display:none}}header nav .burger:hover .burger-line,header nav .burger.open .burger-line{width:30px}header nav .burger-line{background-color:#6730ec;width:30px;height:3px;margin:6px;margin-left:auto;transition:0.25s all ease-in-out}header nav .burger-line:nth-of-type(1){width:40px}header nav .burger-line:nth-of-type(3){width:25px}header nav .burger.open .burger-line:nth-of-type(2){opacity:0;transform:rotate(-225deg) translate(0px, 0px)}header nav .burger.open .burger-line:nth-of-type(1){transform:rotate(-45deg) translate(-6px, 6px)}header nav .burger.open .burger-line:nth-of-type(3){transform:rotate(45deg) translate(-6px, -6px)}.btn{border-radius:5px;padding:15px 25px;border:none;cursor:pointer;font-weight:bold;transition:0.25s all ease;background-color:#6730ec;color:#f1f1f1}.btn.basic{border:2px solid #6730ec;background-color:transparent;color:#6730ec}.btn.basic:hover{background-color:rgba(203,203,203,0.1)}.btn:hover{background-color:#5619ea}

